import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		count: 0,
		loading: false,
		darkTheme: localStorage.darkTheme,
		animations: localStorage.animations
	},
	mutations: {
		increment: state => state.count++,
		decrement: state => state.count--,
		stopLoading: state => state.loading = false,
		startLoading: state => state.loading = true,
		toggleTheme(state) { state.darkTheme = !state.darkTheme, localStorage.darkTheme = !state.darkTheme },
		toggleAnimations(state) { state.animations = !state.animations, localStorage.animations = !state.animations },
	},
	actions: {

	}
})
