<script>
//- @ is an alias to /src
import Title from '@/components/Title.vue'
import HomeTile from '@/components/HomeTile.vue'

export default {
  name: 'home',
  data() {
    return{
      greeting: "¡Hola!"
    }
  },
  components: {
  	Title,
    HomeTile
  },
  beforeMount(){
    this.$store.commit('stopLoading')
  },
  mounted(){

		var hs = new Date().getHours()
		if (hs < 6)				{ this.greeting = '¡Buenas noches!' }
		else if (hs < 15)		{ this.greeting = '¡Buen día!'}
		else if (hs < 21) 		{ this.greeting = '¡Buenas tardes!'}
		else 					{ this.greeting = '¡Buenas noches!'}

  }
}
</script>
