<template lang="pug">
.loadingContainer
  .loadingLoader
    svg.loadingCircular(viewBox="25 25 50 50")
      circle.loadingPath(cx="50" cy="50" r="20" fill="none" stroke-width="5" stroke-miterlimit="10")
</template>
<style lang="stylus">

spinner-width = 3em

.loadingContainer
  width:100%
  height:100%
  position: fixed
  top:0
  left:0
  display:flex
  z-index:20

  
.loadingLoader
  
  position: relative
  margin: auto
  width: spinner-width
  &:before
    content: ''
    display: block
    padding-top: 100%

.loadingCircular, .loadingPath
  animation-delay: 0

.loadingCircular
  animation: rotate 1.5s linear infinite
  height: 100%
  transform-origin: center center
  width: 100%
  position: absolute
  top: 0
  bottom: 0
  left: 0
  right: 0
  margin: auto

.loadingPath 
  stroke: #4E008E
  stroke-dasharray: 1, 200
  stroke-dashoffset: 0
  animation: dash 1.5s ease-in-out infinite
  stroke-linecap: square

@keyframes rotate 
  100% 
    transform: rotate(360deg)

/*@keyframes dash
  0%
    stroke-dasharray: 1, 200
    stroke-dashoffset: 0

  50% 
    stroke-dasharray: 89, 200
    stroke-dashoffset: -35px

  100% 
    stroke-dasharray: 89, 200
    stroke-dashoffset: -124px
*/

@keyframes dash
  0%
    stroke-dasharray: 1, 200
    stroke-dashoffset: 0

  50% 
    stroke-dasharray: 89, 200
    stroke-dashoffset: -35px

  100% 
    stroke-dasharray: 89, 200
    stroke-dashoffset: -124px

</style>
<script>
export default {
  name: 'Loading',
}
</script>