import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    { path: '/', name: 'home', component: Home },
    { path: '/planes', name: 'planes', component: () => import('./views/Planes.vue') },
    { path: '/plan/:carrera', name: 'plan', component: () => import('./views/Plan.vue'), props: true },
    { path: '/calendario', name: 'calendario', component: () => import('./views/Calendario.vue') },
    { path: '/horarios', name: 'horarios', component: () => import('./views/Horarios.vue') },
    { path: '/horario/:carrera', name: 'horario', component: () => import('./views/Horario.vue'), props: true },
    { path: '/apuntes', name: 'apuntes', component: () => import('./views/Apuntes.vue') },
    { path: '/info', name: 'info', component: () => import('./views/Info.vue') },
    { path: '/fotocopias', name: 'fotocopias', component: () => import('./views/Fotocopias.vue') },
    { path: '/mapa', name: 'mapa', component: () => import('./views/Mapa.vue') },
    { path: '/almuerzo', name: 'almuerzo', component: () => import('./views/Almuerzo.vue') },
    { path: '*', component: () => import('./views/404.vue') }
  ]
})
